.footer {
	background-color: $light-navy;
	color: $white;

	&__outer {
		background-color: #165c8a; }

	&__inner {
		max-width: 1076px; }

	&__info {
		padding: 40px 0;
		margin: 0 16px;
		display: flex;
		flex-wrap: wrap;
		@include sm {
			padding-bottom: 20px; } }

	&__contacts {
		width: 100%; }

	&__title {
		font-size: 21px;
		color: $sky-blue;
		font-weight: 400;
		@include sm {
			text-align: center; } }

	&__contacts-list {
		display: flex;
		flex-wrap: wrap;
		margin-top: 13px;
		justify-content: center;
		@include sm {
			justify-content: center; } }

	&__contacts-item {
		display: flex;
		align-items: center;
		&:not(:last-child) {
			margin-right: 71px; }
		.icon {
			width: 24px;
			height: 24px;
			fill: $white; }
		@include sm {
			margin: 0 16px;
			&:not(:last-child) {
				margin-right: 16px; } } }

	&__contacts-link {
		color: $white;
		font-size: 21px;
		margin-left: 10px; }

	&__social {
		margin-top: 40px;
		width: 51%;
		@include sm {
			width: 100%; } }

	&__social-list {
		margin-top: 16px;
		display: flex;
		@include sm {
			justify-content: space-around;
			max-width: 390px;
			margin: 16px auto 0; } }

	&__social-item {
		margin-right: 25px;
		&:hover {
			opacity: 0.7; }
		&:active {
			opacity: 0.85; }
		@include sm {
			margin-right: 0; } }

	&__social-link {
		.icon {
			fill: $white;
			width: 39px;
			height: 39px; } }

	&__subscribe {
		margin-top: 40px;
		width: 49%;
		@include sm {
			width: 100%; } }

	&__subscribe-form {
		margin-top: 7px;
		display: flex;
		@include sm {
			flex-wrap: wrap;
			justify-content: center; } }

	&__subscribe-mail {
		@include notosans;
		font-size: 16px;
		padding: 0 16px;
		width: 100%;
		max-width: 384px;
		color: $cool-grey-two;
		border-radius: 4px;
		&::placeholder {
			color: $silver; }
		@include sm {
			max-width: none;
			min-height: 56px; } }

	&__btn {
		@include notosans;
		max-width: 118px;
		margin-left: 8px;
		@include sm {
			margin-top: 12px; } }

	&__copyrights {
		display: flex;
		justify-content: space-between;
		padding: 23px 0 8px;
		margin: 0 16px; }

	&__copyrights-text {
		@include notosans;
		font-size: 14px;
		letter-spacing: 0.8px; }

	&__technical-list {
		display: flex; }

	&__technical-item {
		margin-left: 24px; }

	&__technical-text {
		vertical-align: top;
		color: $white;
		@include notosans;
		font-size: 14px;
		letter-spacing: 0.8px;
		&:hover {
			opacity: 0.7; }
		&:active {
			opacity: 0.85; } } }
